<template>
    <div class="modalBg" :class="{'modalBg-active': IsOpen === true}">
        <div class="payment" :class="{'payment-active': IsAnimate === true}">
            <h1 class="payment__title">Choose a payment method: <span>{{ cost }}$</span></h1>
            <div class="payment-block">
                <a class="payment__item" data-method="1" @click="makePayment(2)"><img src="../assets/img/svg/logo-main.svg" alt="LogoMain"></a>
                <a class="payment__item" data-method="2" @click="makePayment(5)"><img src="../assets/img/stripe.png" alt="stripe"></a>
            </div>
            <button
            @click="this.$emit('CLoseModal')"
            class="cancel">Cancel</button>
        </div>
    </div>
</template>

<script>
import {mapGetters} from "vuex";
const {useToast} = require("vue-toastification");
import NotifyModal from "@/components/NotifyModal";
export default {
  name: "VPaymentModal",
    props: {
        cost: {
            type: String,
            default(){
                return ""
            }
        },
        IsOpen: {
            type: Boolean,
            default(){
                return false
            }
        }
    },
    data: () => {
        return {
            IsAnimate: false,
            message: '',
            IsNotifyModalOpen: false,
            typeNotifyModal: ''
        }
    },
    watch: {
        IsOpen(){
            setTimeout(() => {
                this.IsAnimate = !this.IsAnimate
            }, 200)

        }
    },
    computed: {
      ...mapGetters([
        'JWT'
      ]),
    },
    methods: {
      CloseNotifyModal(){
        this.IsNotifyModalOpen = false;
      },
      async makePayment(methodId) {
        const toast = useToast();
        try {
          const requestData = {
            method_id: methodId,
            sum: this.cost,
            email: ""
          };
          const response = await this.axios.post('https://api.panaceadayz.us/v2/Payment.create', requestData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          });
          if(response.data.status === 'error')
          {
            toast.error({
              component: NotifyModal,
              props: {
                message: response.data.message,
                type: 'Error!'
              },
            }, {
              position: "top-right",
              timeout: 2000,
              closeOnClick: true,
              pauseOnFocusLoss: true,
              pauseOnHover: true,
              draggable: true,
              draggablePercent: 0.6,
              showCloseButtonOnHover: false,
              hideProgressBar: false,
              closeButton: "button",
              icon: true,
              rtl: false,

            });
            return;
          } else {
            // this.typeNotifyModal = 'success';
            // this.IsNotifyModalOpen = true;
            window.location.href = response.data.link;
          }
        } catch (error) {
          toast.error({
            component: NotifyModal,
            props: {
              message: error.message,
              type: 'Error!'
            },
          }, {
            position: "top-right",
            timeout: 2000,
            closeOnClick: true,
            pauseOnFocusLoss: true,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: false,
            closeButton: "button",
            icon: true,
            rtl: false,

          });
        }
      }
    }
}
</script>

<style lang="sass" scoped>
.payment
    background: url("~@/assets/img/modal_drop_bg.png")
    +bg
    border-radius: 24px
    padding: 32px
    position: relative
    max-width: 340px
    width: 100%
    height: 368px
    transform: perspective(2500px) rotateX(-90deg)
    transition: .6s
    &-active
        transform: perspective(2500px) rotateX(0deg)
    &__title
        font-size: 14px
        font-weight: 500
        span
            font-weight: 700
            color: #ff0000
    &-block
        +flexa
        margin-top: 20px
        justify-content: space-between
        .payment__item
            padding: 0px 3px
            background: linear-gradient(90deg, rgba(211, 16, 39, 0.2) 0%, rgba(234, 56, 77, 0.2) 100%)
            border-radius: 10px
            border: 2px solid transparent
            transition: border 0.2s
            height: 50px
            +flexa
            justify-content: center
            &:hover
                border: 2px solid #ffffff59
    .cancel
        position: absolute
        bottom: 0px
        left: 50%
        transform: translate(-50%, 50%)
        transition: 0.3s all
        background: linear-gradient(90deg, #D31027 0%, #EA384D 100%)
        border-radius: 10px
        font-weight: 500
        font-size: 16px
        padding: 18px 52px
</style>